.search {
    margin: 20px 0 40px;
  }
  .movie-list {
    max-width: 1040px;
    margin: auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 10px;
  }
  .movie-item {
    --width: 200px;
    width: 200px;
  }
  .movie-item .poster {
    width: var(--width);
    height: calc(var(--width) * 3 / 2);
    border-radius: 6px;
    background-color: rgba(0,0,0,.2);
    background-size: cover;
  }
  .observer {
    display: none;
  }
  .loader {
    display: none;
    width: 40px;
    height: 40px;
    margin: 30px auto;
    border: 4px solid orange;
    border-top-color: transparent;
    border-radius: 50%;
    animation: loader 1s infinite linear;
  }
  @keyframes loader {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }